@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;700&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Ubuntu', sans-serif;
}

.hr-line {
  margin-top: 40px;
  margin-bottom: 20px;
  background-color: #000;
  height: 3px;
  animation: hr-animation 5s infinite alternate;
}

.intro-container {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.img-container {
  position: relative;
  margin-bottom: 20px;
}

.img-container::after {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 5px solid white;
  bottom: 20px;
  right: 15px;
  animation: active-status 1s infinite alternate;
}

.profile-img {
  width: 180px;
  height: 180px;
  padding: 8px;
  border-radius: 50%;
  position: absolute;
}

.circular-border {
  width: 180px;
  height: 180px;
  padding: 8px;
  border-radius: 50%;
  border: 2px dashed black;
  z-index: -10;
  animation: spin 10s infinite alternate;
}

.intro-title {
  font-size: 30px;
}
.intro-sub {
  font-size: 12px;
  padding: 0 20px;
  text-align: center;
}
.intro-subtitle {
  margin: 15px 0;
  padding: 0 20px;
  text-align: center;
  width: 80%;
  max-width: 700px;
  line-height: 22px;
}

.social-link {
  color: black;
}

.social-link:hover {
  color: rgba(0, 0, 0, 0.8);
}

.social-link i {
  font-size: 25px;
}

.social-elements {
  display: flex;
  column-gap: 20px;
}

.notes-timeline {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.notes-timeline-title {
  margin: 0 auto;
  text-align: center;
  font-size: 20px;
  background-color: #000;
  color: #fff;
  padding: 10px 20px;
  margin-bottom: 20px;
}


.timeline-tag {
  text-align: center;
  letter-spacing: 5px;
}

.note-container {
  border: 1px solid red;

}

.stories-container {
  width: 100%;
  display: flex;
  margin-top: 20px;
  flex-direction: column;
  padding: 0 20px;
}

.stories-container-title {
  margin: 0 auto;
  text-align: center;
  font-size: 20px;
  background-color: #000;
  color: #fff;
  padding: 10px 20px;
  margin-bottom: 20px;
}

.story-container {
  background-color: #000;
  color: #fff;
  margin-bottom: 20px;
  box-shadow: 4px 5px 10px rgba(0, 0, 0, 0.8);
  padding: 20px;
}

.stories:last-child {
  margin-bottom: -20px;
}

.story-container p {
  margin: 1em 0;
  font-size: .81rem;
  line-height: 1.6;
}

.story-container span {
  opacity: 0.7;
  font-size: 0.81em;
}

.footer {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-bottom: 20px;
}

.footer-text {
  text-align: center;
  padding: 0 20px;
  margin-bottom: 20px;
}

.copyright {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0 20px;
  font-size: 12px;
}

.greetings {
  display: flex;
  column-gap: 15px;

}

.copyright p {
  background-color: #000;
  color: #fff;
  padding: 2px 4px;
}

@keyframes hr-animation {
  0% {
    width: 0%;
    max-width: 0px;
  }

  100% {
    width: 80%;
    max-width: 800px;
  }
}

@keyframes active-status {
  0% {
    background-color: #4caf50;
  }

  100% {
    background-color: #fff;
  }

}

@keyframes spin {
  100% {
    transform: rotateZ(360deg);
  }
}

@media screen and (min-width: 768px) {
  .img-container::after {
    width: 30px;
    height: 30px;
  }

  .profile-img {
    width: 220px;
    height: 220px;
  }

  .circular-border {
    width: 220px;
    height: 220px;
  }

  .intro-title {
    font-size: 40px;
  }

  .social-elements {
    column-gap: 30px;
  }

  .date-color {
    color: #000;
  }

  .stories-container {
    padding: 0 100px;
  }

  .story-container {
    margin-bottom: 0;
  }

  .stories {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    grid-auto-flow: dense;
  }
}